


















import { Component, Watch, Vue, Prop } from 'vue-property-decorator';
import RoutineApi from '@/services/api/RoutineApi';
import Routine from '@/models/Routine';
import ExerciseSet from '@/models/ExerciseSet';
import ExerciseApi from '@/services/api/ExerciseApi';
import Exercise from '@/models/Exercise';
import RoutineExercise from '@/models/RoutineExercise';
import ExerciseConstraint from '@/models/ExerciseConstraint';
import EditExerciseConstraints from '@/components/routine/modals/edit-exercise-constraints-modal.vue';

const exerciseApi = new ExerciseApi();

@Component({
    components: {
        EditExerciseConstraints,
    },
})
export default class RoutinesExerciseSets extends Vue {
    @Prop() private show!: boolean;
    @Prop() private id!: string;
    @Prop() private sets!: ExerciseSet[];

    public openEditModal(exercise: Exercise): void {
        // @ts-ignore
        this.$refs.editModal.open(exercise);
    }

    private editConstraints(payload: any) {
        payload.id = this.id;
        this.$emit('edit-constraints', payload);
    }
}
