
































import { Component, Watch, Vue, Prop } from 'vue-property-decorator';
import RoutineApi from '@/services/api/RoutineApi';
import Routine from '@/models/Routine';
import ExerciseSet from '@/models/ExerciseSet';
import ExerciseApi from '@/services/api/ExerciseApi';
import Exercise from '@/models/Exercise';
import RoutineExercise from '@/models/RoutineExercise';
import ExerciseSearchTemplate from '@/components/exercise/exercise-search-template.vue';

const exerciseApi = new ExerciseApi();

@Component
export default class RoutinesExercise extends Vue {
    @Prop() private id!: string;
    @Prop() private preSelectedExercise!: RoutineExercise | null;
    @Prop() private show!: boolean;
    private sets: number;
    private exercise: RoutineExercise | null;
    private exercises: Exercise[];
    private exerciseSearchTemplate = ExerciseSearchTemplate;

    private selectedExercise: Exercise | null;

    public constructor() {
        super();
        this.exercises = [];

        if (this.preSelectedExercise != null) {
            this.selectedExercise = this.preSelectedExercise.exercise;
            this.sets = this.preSelectedExercise.sets.length > 0 ? this.preSelectedExercise.sets.length : 3;
            this.exercise = this.preSelectedExercise;
        } else {
            this.exercise = null;
            this.sets = 3;
            this.selectedExercise = null;
        }
    }

    private get canShow(): boolean {
        return this.exercise != null && this.selectedExercise != null;
    }

    private toggle() {
        if (this.canShow) {
            this.$emit('toggle-exercise-collapse', this.exercise!.id);
        }
    }

    private remove(): void {
        this.$emit('remove-exercise', this.id);
    }

    @Watch('selectedExercise')
    private exerciseSelected(exercise: Exercise): void {
        this.exercise = new RoutineExercise(exercise, this.sets, this.id, true);
    }

    @Watch('sets')
    private setsUpdated(sets: number) {
        if (this.selectedExercise != null) {
            let show = false;

            if (this.exercise != null) {
                show = this.exercise.show;
            }

            this.exercise = new RoutineExercise(this.selectedExercise, sets, this.id, show);
        }
    }

    @Watch('exercise')
    private exerciseUpdated(exercise: RoutineExercise) {
        this.$emit('exercise-updated', this.exercise);
    }

    private resetSearch() {
        this.exercises = [];
    }

    private fetchExercises(searchText: string): void {
        const result = exerciseApi.get();
        const temp: Exercise[] = [];

        result.forEach((exercise) => {
            if (exercise.name.toLowerCase().match('^.*' + searchText.toLowerCase() + '.*$')) {
                temp.push(exercise);
            }
        });

        this.exercises = temp;
    }

    private getLabel(exercise: Exercise | null): string {
        return exercise !== null ? exercise.name : '';
    }
}
