










































import { Component, Vue } from 'vue-property-decorator';
import RoutineApi from '@/services/api/RoutineApi';
import Routine from '@/models/Routine';
import DeleteRoutine from '@/components/routine/modals/delete-routine-modal.vue';

const routineApi = new RoutineApi();

@Component({
    components: {
        DeleteRoutine,
    },
})
export default class RoutinesList extends Vue {
    private routines: Routine[] = [];

    public openDeleteModal(routine: Routine): void {
        // @ts-ignore
        this.$refs.deleteModal.open(routine.id);
    }

    public submitDeleteModal(): void {
        this.refreshRoutines();
    }

    public mounted(): void {
        this.refreshRoutines();
    }

    public refreshRoutines(): void {
        this.routines = routineApi.get();
    }
}
