import { ExerciseConstraintType } from '@/enums/ExerciseConstraintType';
import Utils from '@/services/Utils';

export default class ExerciseConstraint {
    public id: string;
    public type: ExerciseConstraintType;
    public value: string;

    public constructor(type: ExerciseConstraintType, value: string) {
        this.id = Utils.Guid();
        this.type = type;
        this.value = value;
    }
}
