
























import { Component, Vue, Prop } from 'vue-property-decorator';
import Modal from '@/components/common/Modal.vue';
import ExerciseConstraint from '@/models/ExerciseConstraint';
import { ExerciseConstraintType } from '@/enums/ExerciseConstraintType';
import RoutineApi from '@/services/api/RoutineApi';
import Exercise from '@/models/Exercise';
@Component({
    components: {
        Modal,
    },
})
export default class EditExerciseConstraints extends Vue {
    private constraints: ExerciseConstraint[];
    private id: string;

    public constructor() {
        super();

        this.constraints = [];
        this.id = '';
    }

    public close(): void {
        this.constraints = [];
        this.id = '';
        // @ts-ignore
        this.$refs.modal.showModal = false;
    }

    public submit(): void {
        // @ts-ignore
        this.$refs.modal.showModal = false;

        const tempConstraints: ExerciseConstraint[] = [];

        this.constraints.forEach((constraint) => {
            tempConstraints.push(new ExerciseConstraint(constraint.type, constraint.value));
        });

        this.$emit('submit', { constraints: tempConstraints, exerciseId: this.id});
    }

    public open(exercise: Exercise): void {
        this.id = exercise.id;
        this.constraints = exercise.constraints;
        // @ts-ignore
        this.$refs.modal.showModal = true;
    }

    private constraintTypeName(type: number): string {
        const items: any[] = [];
        const values = Object.keys(ExerciseConstraintType).filter(
            (t) => isNaN(t as any) && t !== 'values',
        );

        if (values.length < 0) {
            return '';
        }

        for (const item of values) {
            // @ts-ignore
            items.push(({ name: item, value: ExerciseConstraintType[item]}));
        }

        return items.find((i) => i.value === type).name;
    }
}
