






































































import { Component, Vue } from 'vue-property-decorator';
import RoutineApi from '@/services/api/RoutineApi';
import Routine from '@/models/Routine';
import ExerciseSet from '@/models/ExerciseSet';
import ExerciseApi from '@/services/api/ExerciseApi';
import Exercise from '@/models/Exercise';
import RoutineExerciseModel from '@/models/RoutineExercise';
import RoutineExercise from '@/components/routine/routine-exercise.vue';
import RoutineExerciseSets from '@/components/routine/routine-exercise-sets.vue';

const routineApi = new RoutineApi();
const exerciseApi = new ExerciseApi();

@Component({
    components: {
        RoutineExercise,
        RoutineExerciseSets,
    },
})
export default class CreateRoutine extends Vue {
    private name: string;
    private exercises: RoutineExerciseModel[];

    public constructor() {
        super();
        this.name = name;
        this.exercises = [];
    }

    private removeExercise(id: string): void {
        const index = this.exercises.findIndex((e) => e.id === id);
        if (index > -1) {
            this.exercises.splice(index, 1);
        }
    }

    private exerciseUpdated(exercise: RoutineExerciseModel) {
        const index = this.exercises.findIndex((re) => re.id === exercise.id);

        if (index > -1) {
            this.exercises.splice(index, 1, exercise);
        }
    }

    private toggleExerciseCollapse(id: string) {
        const index = this.exercises.findIndex((re) => re.id === id);

        if (index > -1) {
            this.exercises[index].show = !this.exercises[index].show;
        }
    }

    private editConstraints(payload: any) {
        const index = this.exercises.findIndex((e) => e.id === payload.id);

        if (index > -1) {
            const exercise = this.exercises[index];

            const setId = exercise.sets.findIndex((s) => s.exercise.id === payload.exerciseId);

            if (setId > -1) {
                exercise.sets[setId].exercise.constraints = payload.constraints;
            }
        }
    }

    private addNewExercise() {
        this.exercises.push(new RoutineExerciseModel());
    }

    private submit(): void {
        const routine = new Routine();
        routine.name = this.name;
        routine.exercises = this.exercises;

        for (const exercise of routine.exercises) {
            exercise.show = false;
        }

        routineApi.post(routine);

        this.$router.push({ name: 'routine-list' });
    }
}
