import ExerciseConstraint from './ExerciseConstraint';
import IStorageItem from './IStorageItem';
import Utils from '@/services/Utils';

export default class Exercise implements IStorageItem {
    public id: string = '';
    public name: string;
    public constraints: ExerciseConstraint[];

    public constructor(name: string, constraints: ExerciseConstraint[], id?: string) {
        this.id = id !== undefined ? id : Utils.Guid();
        this.name = name;
        this.constraints = constraints;
    }
}
