import Dashboard from './views/dashboard/index.vue';
import Account from './views/account/index.vue';
import Settings from './views/settings/index.vue';
import Diary from './views/diary/index.vue';
import Login from './views/login/index.vue';
import Exercises from './views/exercises/index.vue';
import Routines from './views/routines/index.vue';
import RoutinesList from './views/routines/list.vue';
import RoutinesCreate from './views/routines/create.vue';
import RoutinesEdit from './views/routines/edit.vue';

const login = {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
        title: 'Login',
    },
};

const dashboard = {
    path: '/',
    name: 'dashboard',
    component: Dashboard,
    meta: {
        title: 'Dashboard',
        requiresAuthentication: true,
    },
};

const account = {
    path: '/account',
    name: 'account',
    component: Account,
    meta: {
        title: 'Account',
        requiresAuthentication: true,
    },
};

const settings = {
    path: '/settings',
    name: 'settings',
    component: Settings,
    meta: {
        title: 'Settings',
        requiresAuthentication: true,
    },
};

const diary = {
    path: '/diary',
    name: 'diary',
    component: Diary,
    meta: {
        title: 'Diary',
        requiresAuthentication: true,
    },
};

const exercises = {
    path: '/exercises',
    name: 'exercises',
    component: Exercises,
    meta: {
        title: 'Exercises',
        requiresAuthentication: true,
    },
};

const routineList = {
    path: '/routines/',
    name: 'routine-list',
    component: RoutinesList,
    meta: {
        title: 'Routines',
        requiresAuthentication: true,
    },
};

const routineCreate = {
    path: '/routines/create',
    name: 'routine-create',
    component: RoutinesCreate,
    meta: {
        title: 'Create routine',
        requiresAuthentication: true,
    },
};

const routineEdit = {
    path: `/routines/:id([0-9a-fA-F]{8}\-[0-9a-fA-F]{4}\-[0-9a-fA-F]{4}\-[0-9a-fA-F]{4}\-[0-9a-fA-F]{12})`,
    name: 'routine-edit',
    component: RoutinesEdit,
    meta: {
        title: 'Edit routine',
        requiresAuthentication: true,
    },
};

const routines = {
    path: '/routines',
    name: 'routines',
    component: Routines,
    meta: {
        title: 'Routines',
        requiresAuthentication: true,
    },
    children: [
        routineList,
        routineCreate,
        routineEdit,
    ],
};

export default [login, dashboard, account, settings, diary, exercises, routines];
