import Exercise from '@/models/Exercise';
import Utils from '@/services/Utils';
import ExerciseConstraint from './ExerciseConstraint';

export default class ExerciseSet {
    public id: string;
    public exercise: Exercise;

    public constructor(exercise: Exercise) {
        this.id = Utils.Guid();
        const tempConstraints: ExerciseConstraint[] = [];
        for (const constraint of exercise.constraints) {
            tempConstraints.push(new ExerciseConstraint(constraint.type, constraint.value));
        }

        this.exercise = new Exercise(exercise.name, tempConstraints);
    }
}
